import React from 'react'
import PropTypes from 'prop-types'
import ButtonBase from '@material-ui/core/ButtonBase'
import withStyles from './partner-card.style'

const PartnerCard = ({ classes, href, altText, image }) => {
  const handleClick = href => {
    const partnerWindow = window.open(href, '_partner')
    partnerWindow.focus()
  }

  return (
    <ButtonBase
      focusRipple
      key={altText}
      className={classes.root}
      onClick={() => handleClick(href)}
    >
      <img src={image} alt={altText} className={classes.image} />
    </ButtonBase>
  )
}

PartnerCard.propTypes = {
  classes: PropTypes.object.isRequired,
  href: PropTypes.string.isRequired,
  altText: PropTypes.string.isRequired,
  image: PropTypes.node.isRequired,
}

export default withStyles(PartnerCard)
