import React from 'react'
import PropTypes from 'prop-types'
import logo from '../../../images/logos/compose-us-hero.svg'
import withStyles from './hero-image.style'

const HeroImage = ({ classes }) => {
  return (
    <div
      itemScope={true}
      itemType={'https://schema.org/Organization'}
      itemProp={'logo'}
    >
      <img
        src={logo}
        alt="compose.us Gmbh Logo"
        className={classes.root}
        itemProp={'url'}
      />
    </div>
  )
}

HeroImage.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(HeroImage)
