import React from 'react'
import PropTypes from 'prop-types'
import SwipeableViews from 'react-swipeable-views'
import Fab from '@material-ui/core/Fab'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft'
import withStyles from './carousel-component.style'

const CarouselComponent = ({ children, classes }) => {
  const [activeStep, setActiveStep] = React.useState(0)
  const count = React.Children.count(children) - 1

  const clickPrev = () => {
    if (activeStep - 1 < 0) {
      setActiveStep(count)
    } else {
      setActiveStep(previousStep => previousStep - 1)
    }
  }

  const clickNext = () => {
    if (activeStep + 1 > count) {
      setActiveStep(0)
    } else {
      setActiveStep(previousStep => previousStep + 1)
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.wrapper}>
        <SwipeableViews
          index={activeStep}
          onChangeIndex={foo => {
            setActiveStep(foo)
            console.log({ foo })
          }}
          enableMouseEvents
          resistance={true}
        >
          {children}
        </SwipeableViews>
      </div>
      <div className={classes.carouselControls}>
        <Fab
          aria-label="Previous"
          className={classes.carouselControl}
          onClick={() => clickPrev()}
          color={'primary'}
        >
          <ChevronLeftIcon />
        </Fab>
        <Fab
          aria-label="Next"
          className={classes.carouselControl}
          onClick={() => clickNext()}
          color={'primary'}
        >
          <ChevronRightIcon />
        </Fab>
      </div>
    </div>
  )
}

CarouselComponent.propTypes = {
  children: PropTypes.node.isRequired,
}

export default withStyles(CarouselComponent)
