import React from 'react'
import PropTypes from 'prop-types'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ChevronRight from '@material-ui/icons/ChevronRight'
import ListItemText from '@material-ui/core/ListItemText';
import withStyles from './service-item.style'

const ServiceItem = ({ classes, image, label, desc, bullets }) => {
  return (
    <Card
      className={classes.root}
      raised={true}
      itemScope={true}
      itemType={'https://schema.org/Service'}
    >
      <CardContent>
        <img src={image} alt={`${label}: ${desc}`} style={{ height: 177 }} />
        <Typography
          gutterBottom
          variant={'h6'}
          aria-label={label}
          itemProp={'name'}
        >
          {label}
        </Typography>
        <Typography
          variant={'body2'}
          align={'center'}
          component={'div'}
          aria-label={desc}
          itemProp={'description'}
        >
          <List dense disablePadding>
            {bullets.map((bullet, index) => (
              <ListItem key={index} alignItems="flex-start" disableGutters>
                <ListItemIcon className={classes.itemIcon}>
                  <ChevronRight />
                </ListItemIcon>
                <ListItemText className={classes.itemText}>
                  {bullet}
                </ListItemText>
              </ListItem>
            ))}
          </List>
        </Typography>
      </CardContent>
    </Card>
  )
}

ServiceItem.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string.isRequired,
  desc: PropTypes.string.isRequired,
  image: PropTypes.node.isRequired,
  bullets: PropTypes.array.isRequired,
}

export default withStyles(ServiceItem)
