import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const Image = ({
  classes,
  src = 'placeholderImage',
  alt = 'placeholderAlt',
}) => {
  const data = useStaticQuery(graphql`
    query {
      caseStudy01: file(
        relativePath: { eq: "case-studies/01_intl-marketing-technology.jpg" }
      ) {
        ...caseStudiesImage
      }
      caseStudy02: file(
        relativePath: { eq: "case-studies/02_multichannel-retailer.jpg" }
      ) {
        ...caseStudiesImage
      }
      caseStudy03: file(relativePath: { eq: "case-studies/03_intl-oem.jpg" }) {
        ...caseStudiesImage
      }
      caseStudy04: file(
        relativePath: { eq: "case-studies/04_audio-production-publisher.jpg" }
      ) {
        ...caseStudiesImage
      }
      caseStudy05: file(
        relativePath: { eq: "case-studies/05_smart-hotel.jpg" }
      ) {
        ...caseStudiesImage
      }
    }
  `)

  return (
    <Img
      className={classes}
      fluid={data[src].childImageSharp.fluid}
      alt={alt}
    />
  )
}

export const caseStudiesImageFragment = graphql`
  fragment caseStudiesImage on File {
    childImageSharp {
      fluid(maxWidth: 1168) {
        ...GatsbyImageSharpFluid
      }
    }
  }
`

export default Image
