import Grid from '@material-ui/core/Grid'
import PropTypes from 'prop-types'
import React from 'react'
import TechnologyListItem from '../technology-list-item/technology-list-item'

const TechnologyList = ({ list, selected }) => (
  <Grid container spacing={1}>
    {list.map((item, index) => {
      const isSelected = item.categories.indexOf(selected) >= 0
      return (
        <Grid key={index} item>
          <TechnologyListItem
            label={item.label}
            categories={item.categories}
            selected={isSelected}
          />
        </Grid>
      )
    })}
  </Grid>
)

TechnologyList.propTypes = {
  list: PropTypes.array.isRequired,
  selected: PropTypes.string,
}

export default TechnologyList
