import React from 'react'
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button'
import PhoneIcon from '@material-ui/icons/Phone'
import withStyles from './cta-button.style'

const CtaButton = ({ classes, telephone }) => {
  const callTelephone = number => {
    window.location = 'tel:' + number
  }

  return (
    <Button
      variant={'contained'}
      size={'large'}
      color={'primary'}
      className={classes.button}
      onClick={() => callTelephone(telephone)}
      itemProp={'telephone'}
    >
      <PhoneIcon />
      {telephone}
    </Button>
  )
}

CtaButton.propTypes = {
  classes: PropTypes.object.isRequired,
  telephone: PropTypes.string.isRequired,
}

export default withStyles(CtaButton)
