import React from 'react'
import PropTypes from 'prop-types'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import capitalize from '../../../lib/capitalize'

const TechnologyCategoryList = ({ current, categories, callback, mobile }) => {
  let listStyle = {}
  let listItemStyle = { textAlign: 'right' }

  if (mobile) {
    listStyle = {
      ...listStyle,
      display: 'flex',
      flexWrap: 'wrap',
    }
    listItemStyle = {
      ...listItemStyle,
      textAlign: 'center',
      width: '100%',
    }
  }

  return (
    <List dense={true} style={listStyle}>
      {categories.map((category, index) => (
        <ListItem
          key={index}
          button
          style={listItemStyle}
          selected={current === category}
          onClick={() => callback(category)}
        >
          <ListItemText primary={capitalize(category)} />
        </ListItem>
      ))}
    </List>
  )
}

TechnologyCategoryList.propTypes = {
  current: PropTypes.string.isRequired,
  categories: PropTypes.array.isRequired,
  callback: PropTypes.func.isRequired,
  mobile: PropTypes.bool,
}

export default TechnologyCategoryList
