import React from 'react'
import PropTypes from 'prop-types'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import withWidth, { isWidthDown } from '@material-ui/core/withWidth'
import technologies from '../../../lib/technologies'
import TechnologyCategoryList from '../technology-category-list/technology-category-list'
import TechnologyList from '../technology-list/technology-list'
import sortTechnologyCategories from '../../../lib/sort-technology-categories'

const TechnologiesCard = ({ width }) => {
  const categories = sortTechnologyCategories(technologies)
  const [category, setCategory] = React.useState(categories[0])
  const isMorphed = isWidthDown('sm', width)
  const defaultTimeoutLength = 3000
  const prolongedTimeoutLength = 14000
  const [timeoutLength, setTimeoutLength] = React.useState(defaultTimeoutLength)

  React.useEffect(() => {
    const effect = setTimeout(() => {
      const currentIndex = categories.indexOf(category)
      const nextIndex =
        categories.length > currentIndex + 1 ? currentIndex + 1 : 0
      setCategory(categories[nextIndex])
      setTimeoutLength(defaultTimeoutLength)
    }, timeoutLength)
    return () => clearTimeout(effect)
  }, [categories, category, timeoutLength])

  const listChangeHandler = (newCategory) => {
    setTimeoutLength(prolongedTimeoutLength)
    setCategory(newCategory)
  }

  return (
    <Container disableGutters>
      <Card raised={true}>
        <CardContent>
          <Grid container direction={'row'} spacing={2}>
            <Grid item sm={12} md={3}>
              <Typography variant={'h5'} color={'primary'}>
                Technologien
              </Typography>
              <TechnologyCategoryList
                current={category}
                categories={categories}
                callback={listChangeHandler}
                morph={isMorphed}
              />
            </Grid>
            <Grid item sm={12} md={9}>
              <TechnologyList list={technologies} selected={category} />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  )
}

TechnologiesCard.propTypes = {
  width: PropTypes.string.isRequired, // breakpoint string from MUI
}

export default withWidth()(TechnologiesCard)
