import React from 'react'
import PropTypes from 'prop-types'
import { graphql, useStaticQuery } from 'gatsby'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'
import SvgIcon from '@material-ui/core/SvgIcon'
import IconButton from '@material-ui/core/IconButton'
import Divider from '@material-ui/core/Divider'
import withStyles from '@material-ui/styles/withStyles'
import CtaButton from '../../components/cta-button/cta-button'
import SectionHeader from '../layout/sections-header/section-header'
import logo from '../../images/logos/compose-us-hero.svg'
import Partners from '../partner/partner-component/partner-component'
import teamPhoto from '../../images/team-photos/team-composite.png'
import { Hidden } from '@material-ui/core'

const withStyling = withStyles((theme) => ({
  root: {
    ...theme.base.section.root,
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(4, 3),
    },
  },
  wrapper: {
    margin: '0 auto',
    display: 'flex',
    alignItems: 'center',
  },
  team: {
    position: 'relative',
    paddingRight: 25,
  },
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0, 0),
    [theme.breakpoints.down('xs')]: {
      flexDirection: 'column',
    },
  },
  link: {
    borderRadius: 10,
    padding: 0,
    margin: theme.spacing(4, 0, 2, 2),
    boxShadow:
      '0px 3px 1px -2px rgba(0,0,0,0.2), ' +
      '0px 2px 2px 0px rgba(0,0,0,0.14), ' +
      '0px 1px 5px 0px rgba(0,0,0,0.12)',
    '& svg': {
      width: 48,
      height: 48,
    },
    [theme.breakpoints.down('xs')]: {
      margin: theme.spacing(2, 1),
      width: 120,
      height: 120,
      '& svg': {
        width: '100%',
        height: '100%',
        padding: theme.spacing(1),
      },
    },
  },
}))

const ContactUs = ({ classes }) => {
  const { data } = useStaticQuery(graphql`
    {
      data: allSectionsJson(filter: { id: { eq: "contact" } }) {
        nodes {
          id
          title
          desc
          telephone
        }
      }
    }
  `)
  const sectionDetails = data.nodes[0]

  return (
    <Container
      className={classes.root}
      itemScope={true}
      itemType={'https://schema.org/LocalBusiness'}
    >
      <meta itemProp={'name'} content={'compose.us GmbH'} />
      <meta itemProp={'image'} content={logo} />
      <SectionHeader title={sectionDetails.title} />

      <div className={classes.content}>
        <div className={classes.wrapper}>
          <div className={classes.team}>
            <Hidden smDown>
              {/* eslint-disable-next-line jsx-a11y/img-redundant-alt */}
              <img src={teamPhoto} alt="compose.us team photo" />
            </Hidden>
          </div>
          <Typography variant={'body1'} align={'left'} component={'p'}>
            {sectionDetails.desc}
          </Typography>
        </div>
        <CtaButton telephone={sectionDetails.telephone} />

        <IconButton
          color="primary"
          aria-label="LinkedIn"
          className={classes.link}
          href="https://www.linkedin.com/company/compose-us/"
          target="_blank"
          rel="noreferrer"
        >
          <SvgIcon>
            <path d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" />
          </SvgIcon>
        </IconButton>

        <IconButton
          color="primary"
          aria-label="Twitter"
          className={classes.link}
          href="https://twitter.com/compose_us"
          target="_blank"
          rel="noreferrer"
        >
          <SvgIcon>
            <path d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-.139 9.237c.209 4.617-3.234 9.765-9.33 9.765-1.854 0-3.579-.543-5.032-1.475 1.742.205 3.48-.278 4.86-1.359-1.437-.027-2.649-.976-3.066-2.28.515.098 1.021.069 1.482-.056-1.579-.317-2.668-1.739-2.633-3.26.442.246.949.394 1.486.411-1.461-.977-1.875-2.907-1.016-4.383 1.619 1.986 4.038 3.293 6.766 3.43-.479-2.053 1.08-4.03 3.199-4.03.943 0 1.797.398 2.395 1.037.748-.147 1.451-.42 2.086-.796-.246.767-.766 1.41-1.443 1.816.664-.08 1.297-.256 1.885-.517-.439.656-.996 1.234-1.639 1.697z" />
          </SvgIcon>
        </IconButton>
      </div>

      <Divider />
      <Partners />
    </Container>
  )
}

ContactUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyling(ContactUs)
