import React from 'react'
import PropTypes from 'prop-types'
import { graphql, useStaticQuery } from 'gatsby'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import withStyles from '@material-ui/styles/withStyles'
import SectionHeader from '../layout/sections-header/section-header'
import ServiceItem from '../../components/service-item/service-item'

const withStyling = withStyles(theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2, 0),
    marginTop: theme.spacing(2),
  },
}))

const Services = ({ classes }) => {
  const { data } = useStaticQuery(graphql`
    {
      data: allSectionsJson(filter: { id: { eq: "services" } }) {
        nodes {
          id
          title
          services {
            label
            image
            desc
            bullets
          }
        }
      }
    }
  `)
  const sectionDetails = data.nodes[0]
  const services = sectionDetails.services

  return (
    <Container className={classes.root}>
      <SectionHeader title={sectionDetails.title} />
      <Grid
        container
        direction={'row'}
        justify={'space-between'}
        alignItems={'stretch'}
        className={classes.content}
        spacing={2}
      >
        {services.map((service, index) => (
          <Grid item key={index} xs={12} sm={12} md={4}>
            <ServiceItem
              label={service.label}
              desc={service.desc}
              image={service.image}
              bullets={service.bullets}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  )
}

Services.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyling(Services)
