import React from 'react'
import Layout from '../components/layout/layout'
import SEO from '../components/seo/seo'
import Section from '../components/layout/section/section'
import Hero from '../components/media/hero-image/hero-image'
import Services from '../components/use-cases/services'
import SuccessStories from '../components/use-cases/case-studies'
import WhyUs from '../components/use-cases/why-us'
import ContactUs from '../components/use-cases/contact-us'
import { sections } from '../lib/menu-nav'

const IndexPage = () => {
  const sectionId = (id) => {
    const target = sections.filter((section) => section.id === id)
    return target[0].path.replace('/#', '')
  }

  return (
    <Layout>
      <SEO title="compose.us GmbH" />

      <Section id="hero">
        <Hero />
      </Section>

      <Section id={sectionId('services')}>
        <Services />
      </Section>

      <Section id={sectionId('case-studies')}>
        <SuccessStories />
      </Section>

      <Section id={sectionId('qualities')}>
        <WhyUs />
      </Section>

      <Section id={sectionId('contact')}>
        <ContactUs />
      </Section>
    </Layout>
  )
}

export default IndexPage
