import React from 'react'
import PropTypes from 'prop-types'
import { graphql, useStaticQuery } from 'gatsby'
import Container from '@material-ui/core/Container'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ChevronRight from '@material-ui/icons/ChevronRight'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'
import SectionHeader from '../layout/sections-header/section-header'
import TechnologiesCard from '../technology/technologies-card/technologies-card'

const withStyling = withStyles(theme => ({
  root: {
    ...theme.base.section.root,
    padding: theme.spacing(5),
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(4, 3),
    },
  },
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
  },
  itemIcon: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
}))

const WhyUs = ({ classes }) => {
  const { data } = useStaticQuery(graphql`
    {
      data: allSectionsJson(filter: { id: { eq: "why-us" } }) {
        nodes {
          id
          title
          reasons {
            text
          }
        }
      }
    }
  `)
  const { title, reasons } = data.nodes[0]

  return (
    <Container className={classes.root}>
      <SectionHeader title={title} />
      <div className={classes.content}>
        <List itemScope={true} itemType={'https://schema.org/BusinessAudience'}>
          {reasons.map(({ text }, index) => (
            <ListItem key={index} alignItems={'flex-start'} dense={true}>
              <ListItemIcon className={classes.itemIcon}>
                <ChevronRight />
              </ListItemIcon>
              <ListItemText
                primary={<Typography variant={'body1'}>{text}</Typography>}
                itemProp={'potentialAction'}
              />
            </ListItem>
          ))}
        </List>
      </div>
      <TechnologiesCard />
    </Container>
  )
}

WhyUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyling(WhyUs)
