const sortTechnologyCategories = (technologies) => {
  const possibleTechnologies = technologies.reduce(
    (acc, { categories }) => ({
      ...acc,
      ...categories.reduce((cats, c) => ({ ...cats, [c]: false }), {}),
    }),
    {}
  )

  return Object.keys(possibleTechnologies).sort()
}

export default sortTechnologyCategories
